html, body {
    font-family: Helvetica;
    margin: 0 0 0 0 !important;
    font-size: 11pt;
    height: 100%;
    line-height: 130%;
    min-height: 100%;
    z-index: 0;
  }
  
  body {
    background-color: white;
  }
  
  a {
    text-decoration: none;
    color: #2951CC;
  }
  
  div {
    word-wrap: break-word;
  }
  
  .white {
    background-color: white;
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    width: 100%;
  }
  
  .main-container, .navbar-container{
    /*max-width: 880px;*/
    margin-right: auto;
    margin-left: auto;
    padding: 30px 40px 10px 40px;
    background-color: rgba(256,256,256,0.9);
    z-index: 1;
  }
  
  .navbar-brand {
    padding-left: 0;
    margin-left: 0 !important;
    z-index: 1;
  }
  
  .navbar-container {
    padding: 5px 40px 5px 40px;
    z-index: 1;
  }
  
  .navbar-toggle {
    margin-right: 0 !important;
    z-index: 1;
  }
  
  .content {
    padding: 30px 30px 10px 30px;
  }
  
  .span3 {
    width: 100%;
    padding: 10px 0 10px 0;
  }
  
  .span2 {
    width: 65%;
    float: left;
    padding: 10px 50px 10px 0;
  }
  
  .span2-nopad {
    width: 70.5%;
    float: left;
    padding: 10px 0 10px 0;
  }
  
  .span1 {
    width: 29%;
    float: left;
    padding: 10px 0 10px 0;
  }
  
  h1 {
    font-size: 18pt;
    margin: 0 0 0 0;
    line-height: 140%;
  }
  
  h2 {
    font-size: 15pt;
    font-weight: bold;
    margin: 0 0 0 0;
    line-height: 140%;
    padding-bottom: 10px;
  }
  
  h3 {
    font-size: 13pt;
    margin: 0 0 0 0;
    font-weight: bold;
    line-height: 130%;
    padding-top: 10px;
    padding-bottom: 5px;
  }
  
  body {
    padding-top: 55px;
    position: relative;
  }
  
  nav.navbar {
    background: white;
    z-index: 1;
  }
  
  a.anchor{
    display: block; 
    position: relative; 
    top: -85px; 
    visibility: hidden;
  }
  
  .row {
    padding: 0 15px 30px 15px;
  }
  
  .center {
    text-align: center;
  }
  

  
  .table-hover tbody tr:hover {
    background-color: rgba(50, 176, 226, 0.15) !important;
  }

  .table-hover tbody tr:first-child:hover {
    background-color: white;
  }
  
  img.headshot {
    width: 100%;
    margin-bottom: 10px;
    opacity: 0.9;
    transition: opacity .3s ease-in-out;
    -moz-transition: opacity .3s ease-in-out;
    -webkit-transition: opacity .3s ease-in-out;
  }
  
  img.headshot:hover {
    opacity: 1.0;
    transition: opacity .3s ease-in-out;
    -moz-transition: opacity .3s ease-in-out;
    -webkit-transition: opacity .3s ease-in-out;
  }
  
  .bottomspace {
    margin-bottom: 20px;
  }
  
  .scrollable { 
    overflow: auto; 
    white-space: nowrap;
  }
  
  td.txt {
    padding: 2px 8px;
    background-color: white !important;
  }
  
  table, th, td {
    border: 1px solid black;
    margin: 0 auto;
  }
  
  .navbar-default .navbar-nav>.active>a {
    border-radius: 15px;
    background-color: rgba(50, 176, 226, 0.15) !important;
  }
  
  li.dropdown {
    display: inline-block;
  }
  
  * {
    font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
        Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
        "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
        "Noto Color Emoji";
}

::selection {
    background-color: hsla(209, 46%, 35%, 0.25);
}

