/* adapted from bootstrap sticky-top */
@supports ((position: -webkit-sticky) or (position: sticky)) {
    .fc-toolbar {
        position: -webkit-sticky;
        position: sticky;
        top: 0px;
        z-index: 1020;
        background: #fff;
    }
    .fc-head {
        position: -webkit-sticky;
        position: sticky;
        top: 54px;
        z-index: 1030;
        background: #fff;
    }
}

